document.addEventListener( 'DOMContentLoaded', ()=>{
    crearGaleria();
});

function crearGaleria(){
    const galeria = document.querySelector('.galeria-imagenes');

    for( let i=1; i<=12; i++ ){
        const imagen = document.createElement('IMG');
        imagen.src = `build/img/thumb/${i}.webp`;
        imagen.dataset.imagenId = i;
        //agreagr a la imagen la funcion
        imagen.onclick = mostrarImagen;

        const lista = document.createElement('LI');
        lista.appendChild( imagen );
        galeria.appendChild(lista);
    }
}


function mostrarImagen(e){

    const id = parseInt(e.target.dataset.imagenId);
    console.log( id );
    
    //Se crea la imagen
    const imagenGrande = document.createElement('IMG');
    imagenGrande.src = `build/img/grande/${id}.webp`;

    //  Se crea el contenedor y se agregar la imagen   
    const overlay = document.createElement('DIV');
    overlay.classList.add('overlay');
    overlay.appendChild( imagenGrande );
    //Cerrar overlay dondo click afuera
    overlay.onclick = ()=>{
        overlay.remove();
        body.classList.remove('fijar-body');
    }

    //Boton para cerrar la imagen
    const btnCerrar = document.createElement('P');
    btnCerrar.textContent = 'X';
    btnCerrar.classList.add('btn-cerrar');
    
    //cerrar imagen
    btnCerrar.onclick = ()=>{
        overlay.remove();
        body.classList.remove('fijar-body');
    }

    //Agregar el boton de cerrar al contenedor(Overlay)
    overlay.appendChild( btnCerrar );

    
    // Se muestra el contenedor con la imegen en el body
    const body = document.querySelector('body');
    body.appendChild( overlay );
    body.classList.add('fijar-body');
    
}
